$backgroundColor: #ffffff;
$bodyColor: #000000;
$bodyFont: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
$linkColor: #212529 !important;
@import 'academicons';

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
}

body a {
  color: $linkColor
}

/* reuse existing class but give a bigger, clearer hero treatment */
.hero-socials a {
  font-size: 2.05rem;     /* bigger than footer */
  color: inherit;
  text-decoration: none;
  opacity: 0.85;
}

.hero-socials a:hover {
  opacity: 1;
}

/* Optional: if you want the row to align nicely with text */
.hero-socials {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 0.25rem;           /* bootstrap spacing still does most of it */
}

