/*  Duplication of the FontAwesome style classes using 'ai' in place of 'fa'. */
.ai-lg {
  font-size: 1.33333em;
  line-height: 0.75em;
  vertical-align: -.0667em;
}
.ai-xs {
  font-size: .75em;
}
.ai-sm {
  font-size: .875em;
}

.ai-1x {
  font-size: 1em;
}
.ai-2x {
  font-size: 2em;
}
.ai-3x {
  font-size: 3em;
}
.ai-4x {
  font-size: 4em;
}
.ai-5x {
  font-size: 5em;
}
.ai-6x {
  font-size: 6em;
}
.ai-7x {
  font-size: 7em;
}
.ai-8x {
  font-size: 8em;
}
.ai-9x {
  font-size: 9em;
}
.ai-10x {
  font-size: 10em;
}

.ai-fw {
  text-align: center;
  width: 1.25em;
}

.ai-ul {
  list-style-type: none;
  margin-left: 2.5em;
  padding-left: 0;
}
.ai-ul > li {
    position: relative;
}
.ai-li {
  left: -2em;
  position: absolute;
  text-align: center;
  width: 2em;
  line-height: inherit;
}

.ai-border {
border: solid 0.08em #eee;
border-radius: .1em;
  padding: .2em .25em .15em;
}

.ai-pull-left {
  float: left;
}
.ai-pull-right {
  float: right;
}
.ai.ai-pull-left {
  margin-right: .3em;
}
.ai.ai-pull-right {
  margin-right: .3em;
}

.ai-stack {
  display: inline-block;
  height: 2em;
  line-height: 2em;
  position: relative;
  vertical-align: middle;
  width: 2.5em;
}
.ai-stack-1x,
.ai-stack-2x {
  left: 0;
  position: absolute;
  text-align: center;
  width: 100%;
}
.ai-stack-1x {
  line-height: inherit;
}
.ai-stack-2x {
  font-size: 2em;
}
.ai-inverse {
  color: #fff;
}
